<template>
</template>

<script>
import { resoucePath } from '../helper';
export default {
  methods: {
    startSound() {
      let path = resoucePath(false) + '/sound/effect/application-start.mp3';
      if (this.audio != null) {
        this.audio.pause()
        this.audio = null;
      }
      this.audio = new Audio(path);
      this.audio.loop = false;
      this.audio.play();
    }
  },
  created() {
    this.$router.push({ path: '/lesson-info/1' })
    // this.startSound();
    // let _this = this;
    // setTimeout(() => {
    //   _this.$router.push({ path: '/lesson-info/1' })
    // }, 2500)
  },
  computed: {
    logoImg: function() {
      return resoucePath(true) + '/img/lingotalk.png';
    }
  }
}
</script>

<style scoped>

</style>



