/*eslint-disable */
import { mp3FileName, resoucePath } from '../helper';
import { LIVE2D_STATE } from './state';

export default class Live2d {
  Animate() {
    // this.micAnimation();
    this.renderer.render(this.stage);
    const _this = this;
    requestAnimationFrame(function() {
      _this.Animate();
    });
  }

  micAnimation() {
    if (this.state !== LIVE2D_STATE.PLAY) return;
    if (!this.micGroup.visible) return;

    this.circleSize += 0.3;
    if (this.circleSize > 55) {
      this.circleSize = 43;
    }
    this.micOut1.clear();
    this.micOut1.beginFill(0xffffff, 0.7);
    this.micOut1.drawCircle(400, 400, this.circleSize);

    // this.micOut2.rotation = 2;
  }

  // 마이크 진행 표시하기
  micPercent(startTime, limtSec) {
    let current = new Date().getTime();
    let percent = (current - startTime) / (limtSec * 10);
    // percent = percent%100;
    this.micOut2.clear();
    this.micOut2.beginFill(0x080a7b, 0.7);
    this.micOut2.moveTo(400, 400);
    if (percent !== 0) {
      if (percent > 100) percent = 100;
      let angle = parseFloat(percent) / 50.0;
      angle = Math.PI * angle + 4.7;
      this.micOut2.arc(400, 400, 43, Math.PI * 1.5, angle);
    }
    this.micOut2.lineTo(400, 400);
    this.micOut2.endFill();
  }

  micShow(visible) {
    if (this.state === LIVE2D_STATE.PLAY) this.micGroup.visible = visible;
  }

  // 마이크 출력
  mic() {
    this.micOut1 = new PIXI.Graphics();
    this.micOut1.lineStyle(0);
    this.micOut1.beginFill(0xffffff, 0.7);
    this.micOut1.drawCircle(400, 400, 60);

    this.micOut2 = new PIXI.Graphics();
    this.micOut2.lineStyle(0);
    this.micOut2.beginFill(0x00ffff, 0.7);
    this.micOut2.drawCircle(400, 400, 43);

    this.micOut3 = new PIXI.Graphics();
    this.micOut3.lineStyle(0);
    this.micOut3.beginFill(0xff0000, 1);
    this.micOut3.drawCircle(400, 400, 35);

    this.micImg = new PIXI.Sprite.fromImage(resoucePath(true) + '/img/mic.png');
    this.micImg.anchor.set(0.5);
    this.micImg.width = 50;
    this.micImg.height = 50;
    this.micImg.x = 400;
    this.micImg.y = 400;

    this.micGroup = new PIXI.Container();
    this.micGroup.addChild(this.micOut1);
    this.micGroup.addChild(this.micOut2);
    this.micGroup.addChild(this.micOut3);
    this.micGroup.addChild(this.micImg);

    this.stage.addChild(this.micGroup);
    this.micGroup.visible = false;
    this.micShow(false);
  }

  // 로딩 화면 세팅
  setLoading(lesson, model, dialog) {
    this.state = LIVE2D_STATE.LOADING;
    const _this = this;
    let loader = new PIXI.loaders.Loader();
    loader.add(model.textures);
    loader.add(resoucePath(true) + '/assets/image/' + lesson.background);
    loader.add(resoucePath(true) + '/img/mic.png');
    loader.add(resoucePath(true) + '/img/skip.png');
    for (let i = 0; i < dialog.length; i++) {
      loader.add('/' + mp3FileName(dialog[i]));
    }

    let background = new PIXI.Graphics();
    background.beginFill(0xffffff, 1);
    background.drawRect(0, 0, 800, 600);
    this.stage.addChild(background);

    let style = new PIXI.TextStyle({
      align: 'center',
      fontFamily: 'Arial',
      fontSize: 20,
      fontWeight: 'bold',
      fill: ['#ffffff', '#ffffff'], // gradient
      stroke: '#4a1850',
      strokeThickness: 2,
      dropShadow: true,
      dropShadowColor: '#000000',
      dropShadowBlur: 2,
      dropShadowDistance: 2,
      wordWrap: true,
      wordWrapWidth: 400
    });

    const bgimage = new PIXI.Sprite.fromImage(
      resoucePath(true) + '/img/lingotalk.png'
    );
    bgimage.width = 300;
    bgimage.height = 150;
    bgimage.anchor.set(0.5);
    bgimage.x = 400;
    bgimage.y = 200;
    this.stage.addChild(bgimage);

    this.loadingProgress1 = new PIXI.Graphics();
    this.loadingProgress1.lineStyle(0);
    this.loadingProgress1.beginFill(0xffffff, 1);
    this.loadingProgress1.drawCircle(400, 400, 60);
    this.stage.addChild(this.loadingProgress1);

    this.loadingProgress2 = new PIXI.Graphics();
    this.loadingProgress2.lineStyle(0);
    this.loadingProgress2.beginFill(0xffffff, 0.7);
    this.loadingProgress2.drawCircle(400, 400, 60);
    this.stage.addChild(this.loadingProgress2);

    this.loadingProgress3 = new PIXI.Graphics();
    this.loadingProgress3.lineStyle(0);
    this.loadingProgress3.beginFill(0xff5555, 1);
    this.loadingProgress3.drawCircle(400, 400, 40);
    this.stage.addChild(this.loadingProgress3);

    this.loadingProgressText = new PIXI.Text('', style);
    this.loadingProgressText.x = 325;
    this.loadingProgressText.y = 390;

    this.stage.addChild(this.loadingProgressText);


    loader.on('progress', function(loader, res) {
      let percent =  parseInt(loader.progress)
      _this.loadingProgress2.clear();
      _this.loadingProgress2.beginFill(0x003060, 0.7);
      _this.loadingProgress2.moveTo(400, 400);

      if (percent !== 0) {
        if (percent > 100) percent = 100;
        let angle = parseFloat(percent) / 50.0;
        angle = Math.PI * angle + 4.7;
        _this.loadingProgress2.arc(400, 400, 60, Math.PI * 1.5, angle);
      }
      _this.loadingProgress2.lineTo(400, 400);
      _this.loadingProgress2.endFill();

      _this.loadingProgressText.text = percent + '%\n\n\nNOW LOADING';
    });

    // listen to the complete event, it will be fired when everything is loaded
    loader.on('complete', function(loader, res) {
      _this.setPlay(lesson, model);
    });
    loader.load();
  }

  // 플레이 화면 세팅
  setPlay(lesson, model) {
    const _this = this;
    this.bSkip = false;

    this.circleSize = 40.0;
    this.isSoundPlaying = false;

    const bg = new PIXI.Sprite.fromImage(
      resoucePath(true) + '/assets/image/' + lesson.background
    );
    bg.width = 800;
    bg.height = 600;
    this.stage.addChild(bg);

    // Live2D Sprite 생성
    this.live2dSprite = new PIXI.Live2DSprite(model, {
      debugLog: false,
      randomMotion: true,
      eyeBlink: true,
      lipSyncWithSound: true
    });

    this.stage.addChild(this.live2dSprite);

    this.live2dSprite.adjustScale(
      model.adjustScale[0],
      model.adjustScale[1],
      model.adjustScale[2]
    );
    this.live2dSprite.adjustTranslate(
      model.adjustTranslate[0],
      model.adjustTranslate[1]
    );
    // 랜덤 idle
    this.live2dSprite.startRandomMotion('idle');

    // 마우스 시선
    // const _live2dSprite = this.live2dSprite;
    // this.live2dSprite.on("mousemove", evt => {
    //   const point = evt.data.global;
    //   _live2dSprite.setViewPoint(point.x, point.y);
    // });

    this.mic();

    var style = new PIXI.TextStyle({
      align: 'center',
      fontFamily: 'Arial',
      fontSize: 36,
      fontWeight: 'bold',
      fill: ['#ffffff', '#ffffff'], // gradient
      stroke: '#4a1850',
      strokeThickness: 5,
      dropShadow: true,
      dropShadowColor: '#000000',
      dropShadowBlur: 2,
      dropShadowAngle: Math.PI / 6,
      dropShadowDistance: 2,
      wordWrap: true,
      wordWrapWidth: 400
    });

    this.sayText = new PIXI.Text('', style);
    this.sayText.x = 30;
    this.sayText.y = 180;

    this.stage.addChild(this.sayText);

    var talkStyle = {
      default: {
        align: 'center',
        fontFamily: 'Arial',
        fontSize: 36,
        fontWeight: 'bold',
        fill: '#ffffff', // gradient
        stroke: '#4a1850',
        strokeThickness: 5,
        dropShadow: true,
        dropShadowColor: '#000000',
        dropShadowBlur: 2,
        dropShadowAngle: Math.PI / 6,
        dropShadowDistance: 2,
        wordWrap: true,
        wordWrapWidth: 740
      },
      pass: {
        fill: '#ff8888'
      }
    };

    this.talkText = new MultiStyleText('', talkStyle);
    this.talkText.x = 30;
    this.talkText.y = 450;

    this.stage.addChild(this.talkText);

    this.btnSkip = new PIXI.Sprite.fromImage(resoucePath(true) + '/img/skip.png');
    this.btnSkip.width = 80;
    this.btnSkip.height = 40;
    this.btnSkip.x = 690;
    this.btnSkip.y = 530;
    this.btnSkip.interactive = true;
    this.btnSkip.buttonMode = true;
    this.btnSkip.on('pointerdown', () => {
      _this.btnSkip.visible = false;
      _this.bSkip = true;
    });
    this.btnSkip.visible = false;
    this.stage.addChild(this.btnSkip);
    this.state = LIVE2D_STATE.PLAY;
  }

  constructor(lesson, dialog) {
    const _this = this;
    this.state = LIVE2D_STATE.LOADING;

    this.renderer = new PIXI.WebGLRenderer(800, 600, {
      transparent: true,
      antialias: true
    });
    document.getElementById('lesson').appendChild(this.renderer.view);
    this.stage = new PIXI.Container();

    const model = process.env.NODE_ENV == "development" ? 
      require('./character/' + lesson.model) : 
      require('./character2/' + lesson.model);
    this.setLoading(lesson, model, dialog);

    this.Animate();
  }

  SetText(text, flag) {
    if (this.state !== LIVE2D_STATE.PLAY) return;
    if (flag == 'say') {
      this.sayText.text = text;
      this.talkText.text = '';
      this.btnSkip.visible = false;
    } else {
      // this.sayText.text = '';
      this.talkText.text = text;
      this.btnSkip.visible = true;
    }
  }

  SetMotion() {
    this.live2dSprite.startRandomMotionOnce('motion', 10)
  }

  PlaySound(path) {
    if (this.state !== LIVE2D_STATE.PLAY) return;
    this.SetMotion();
    this.live2dSprite.playSound(path, '/');
    let _this = this;

    // 사운드를 재생중인지 체크
    this.isSoundPlaying = true;
    this.soundPlayCheck = setInterval(() => {
      try {
        if (!this.live2dSprite.model.audioElement.ended) {
          _this.isSoundPlaying = true;
        } else {
          _this.isSoundPlaying = false;
          // this.live2dSprite.model.audioElement.close();
          clearInterval(_this.soundPlayCheck);
        }
      } catch (e) {
        // this.live2dSprite.model.audioElement.ended 가 없을 경우가 있음.
        _this.isSoundPlaying = true;
      }

    }, 300);
  }

  Destroy() {
    if (this.live2dSprite) this.live2dSprite.destroy();
    this.renderer.destroy(true);
  }
}

/*eslint-enable */



