<template>
  <v-app light id="e3" toolbar>
    <v-navigation-drawer class="pb-0" persistent height="100%" clipped absolute enable-resize-watcher v-model="drawer">
      <v-list dense>
        <v-subheader class="mt-3 grey--text text--darken-1">LESSONS</v-subheader>
        <v-list-tile v-for="item in items" :key="item.text" @click="navi(item.id)">
          <v-list-tile-action>
            <v-icon :class="[currentSelect==item.id?'teal--text':'grey--text']">{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              {{ item.id + ". " + item.title }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-subheader class="mt-3 grey--text text--darken-1">SUBSCRIPTIONS</v-subheader>
        <v-list-tile class="mt-3" @click="">
          <v-list-tile-action>
            <v-icon class="grey--text text--darken-1">add_circle_outline</v-icon>
          </v-list-tile-action>
          <v-list-tile-title class="grey--text text--darken-1">Browse Channels</v-list-tile-title>
        </v-list-tile>
        <v-list-tile @click="">
          <v-list-tile-action>
            <v-icon class="grey--text text--darken-1">settings</v-icon>
          </v-list-tile-action>
          <v-list-tile-title class="grey--text text--darken-1">Manage Subscriptions</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar class="red" fixed>
      <v-toolbar-title>
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <v-icon class="ml-3">fa-youtube</v-icon>
        {{ items[currentSelect-1].title}}
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
    <main>
      <v-container fluid>
        <transition name="fade" mode="out-in">
          <router-view></router-view>
        </transition>
      </v-container>
    </main>
  </v-app>
</template>

<script>
import lessons from '../data/lessons'
import eventBus from './EventBus.vue'
export default {
  data() {
    return {
      clipped: true,
      drawer: true,
      items: lessons,
      miniVariant: false,
      title: 'Talklish',
      currentSelect: 1
    }
  },
  created: function() {
    eventBus.$on('select-lesson', this.changeSelect);
  },
  methods: {
    navi(id) {
      this.currentSelect = id
      this.$router.push({
        name: 'lesson-info',
        params: {
          id: id
        }
      })
    },
    changeSelect(id) {
      this.currentSelect = id
    }
  }
}
</script>

<style scoped>
.list__tile__title {
  text-decoration: none;
}

@media screen and (max-width: 600px) and (orientation: portrait) {
  .navigation-drawer--permanent.navigation-drawer--clipped,
  .navigation-drawer--persistent.navigation-drawer--clipped,
  .navigation-drawer--permanent.navigation-drawer--floating,
  .navigation-drawer--persistent.navigation-drawer--floating {
    margin-top: 54px;
  }
}

.list__tile__action {
  min-width: 30px;
}


/* 애니메이션 */

.fade-enter-active,
.fade-leave-active {
  transition-property: opacity;
  transition-duration: .25s;
}

.fade-enter-active {
  transition-delay: .25s;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}

.overlay,
.navigation-drawer--absolute,
.overlay--active,
.overlay--active {
  position: fixed;
}
</style>



