export default class VoiceRecognition {
  Init() {
    var _this = this;
    if (!('webkitSpeechRecognition' in window)) {
      this.Upgrade();
    } else {
      // start_button.style.display = 'inline-block';
      /*eslint-disable */
      this.recognition = new webkitSpeechRecognition();
      /*eslint-enable */
      this.recognition.continuous = true;
      this.recognition.interimResults = true;
      this.recognition.lang = 'en-US';
      this.recognition.ignore_onend = false;
      this.tryCount = 0;

      this.recognition.onstart = function() {
        _this.final_transcript = '';
        _this.recognizing = true;
        _this.tryCount = 0;
      };

      this.recognition.onend = function() {
        _this.recognizing = false;
      };

      this.recognition.onerror = function(e) {
        console.log(e);
        _this.recognizing = false;
      };

      // 음성 인식 결과
      this.recognition.onresult = function(event) {
        _this.transcript = '';
        for (var i = event.resultIndex; i < event.results.length; ++i) {
          if (event.results[i].isFinal) {
            _this.tryCount++;
          }
          if (event.results[i][0].transcript) {
            _this.transcript += event.results[i][0].transcript;
          }
        }
        if (_this.transcript) {
          _this.final_transcript = _this.transcript;
        }
        // console.log(_this.final_transcript);
      };
    }
  }

  constructor() {
    this.recognizing = false;
    this.recognition = false;

    this.ignore_onend = false;
    this.two_line = /\n\n/g;
    this.one_line = /\n/g;

    // 인식중인 글자
    this.transcript = '';
    // 최종글자
    this.final_transcript = '';

    this.Init();
  }

  GetText() {
    return this.transcript;
  }

  ShowInfo(s) {
    console.log(s);
  }

  Upgrade() {
    alert('크롬 웹프라우저 버전 25 이상을 사용해 주세요.');
  }

  Capitalize(s) {
    return s.replace(this.first_char, function(m) {
      return m.toUpperCase();
    });
  }

  LineBreak(s) {
    return s.replace(this.two_line, '<p></p>').replace(this.one_line, '<br>');
  }

  Start() {
    if (this.recognizing === true) return;
    this.final_transcript = this.transcript = '';
    try {
      this.recognition.start();
    } catch (e) {
      this.Stop();
      window.history.back();
    }
  }

  Stop() {
    this.recognizing = false;
    this.recognition.stop();
  }

  Destroy() {
    this.recognition.stop();
    this.recognition = null;
  }

  ResetText() {
    this.final_transcript = '';
  }
}



