
<template>
  <div>
    <v-card style="width:800px;">
      <v-toolbar :class="{red:mode=='traning', green:mode=='practice'}" dark>
        <v-toolbar-title> {{ id }}. {{ mode|uppercase }} / {{ lesson.title }}</v-toolbar-title>
      </v-toolbar>
        <div id="lesson" style="width:100%"></div>
    </v-card>
  </div>
</template>

<script>
// import Router from 'vue-router';
import lessons from '../data/lessons'
import data from '../data/data'
import eventBus from './EventBus.vue'
import Speech from '../speech'

export default {
  name: 'lesson',
  props: [
    'mode',
    'id'
  ],
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      windowSize: {
        x: 0,
        y: 0
      },
      voiceText: ''
    }
  },
  computed: {
    lesson: function() {
      let id = this.id - 1
      return lessons[id]
    },
    dialog: function() {
      let id = this.id
      return data.filter(function(d) {
        return parseInt(d.lesson) === parseInt(id) && d.category === "dialog"
      })
    }
  },
  created() {
    window.scrollTo(0, 0);
    eventBus.$emit('select-lesson', this.id);
    eventBus.$on('lesson-end', this.lessonEnd);
  },
  mounted() {
    this.onResize()
    this.speech = new Speech(this.lesson, this.mode, this.dialog)
  },
  methods: {
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
    lessonEnd(data) {
      this.$store.dispatch('setLessonData', data);
      this.$router.push({ path: '/lesson/result/' + this.mode + '/' + this.id })
    }
  },
  filters: {
    uppercase: function(v) {
      return v.toUpperCase();
    }
  },
  destroyed() {
    this.speech.Destroy();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>




