<template>
  <div class="hello">
    <h1>{{ id }}. {{ lesson.title }}</h1>
    <v-container fluid grid-list-md>
      <v-layout row wrap>
        <v-flex d-flex xs6 sm6>
          <v-card dark class="purple">
            <v-card-text>
              <v-icon class="white--text">format_color_text</v-icon>
              &nbsp;KEY WORD
            </v-card-text>

            <v-list two-line>
              <template v-for="w in words">
                <v-divider></v-divider>
                <v-list-tile @click="playSound(w)">
                  <v-list-tile-content>
                    <v-list-tile-title v-html="w.content"></v-list-tile-title>
                    <v-list-tile-sub-title v-html="w.trans"></v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
</template>
              <template v-for="i in wordRest">
  <v-divider>
  </v-divider>
  <v-list-tile>
    <v-list-tile-content>
      <v-list-tile-title></v-list-tile-title>
      <v-list-tile-sub-title></v-list-tile-sub-title>
    </v-list-tile-content>
  </v-list-tile>
</template>
          </v-list>
              </v-card>
    </v-flex>


<v-flex d-flex xs6 sm6>
        <v-card dark class="blue">
          <v-card-text>
            <v-icon class="white--text">question_answer</v-icon>
            &nbsp;CORE SENTENCE
          </v-card-text>
          <v-list two-line>
<template v-for="c in cores">
  <v-divider>
  </v-divider>
  <v-list-tile @click="playSound(c)">
    <v-list-tile-content>
      <v-list-tile-title v-html="c.content"></v-list-tile-title>
      <v-list-tile-sub-title v-html="c.trans"></v-list-tile-sub-title>
    </v-list-tile-content>
  </v-list-tile>
</template>
<template v-for="i in coreRest">
  <v-divider>
  </v-divider>
  <v-list-tile>
    <v-list-tile-content>
      <v-list-tile-title></v-list-tile-title>
      <v-list-tile-sub-title></v-list-tile-sub-title>
    </v-list-tile-content>
  </v-list-tile>
</template>
          </v-list>
        </v-card>
</v-flex>
</v-layout>

    </v-container>
    <div class="my-4"></div>
    <v-btn block success large @click="$router.push({name:'lesson', params:{mode:'practice',id:id}})">PRACTICE</v-btn>
    <v-btn block error large @click="$router.push({name:'lesson', params:{mode:'traning',id:id}})">TRANING</v-btn>
  </div>
</template>

<script>
import lessons from '../data/lessons'
import data from '../data/data'
import eventBus from './EventBus.vue'
import { mp3FileName } from '../helper'

export default {
  name: 'lesson-info',
  props: [
    'id'
  ],
  data: function() {
    return {
      autio: null,
      lesson_data: []
    }
  },
  computed: {
    lesson: function() {
      let id = this.id - 1
      return lessons[id]
    },
    words: function() {
      let id = this.id
      return data.filter(function(d) {
        return parseInt(d.lesson) === parseInt(id) && d.category === "word"
      })
    },
    cores: function() {
      let id = this.id
      return data.filter(function(d) {
        return parseInt(d.lesson) === parseInt(id) && d.category === "core"
      })
    },
    wordRest: function() {
      let cnt = this.cores.length - this.words.length;
      return cnt < 0 ? 0 : cnt;
    },
    coreRest: function() {
      let cnt = this.words.length - this.cores.length;
      return cnt < 0 ? 0 : cnt;
    }
  },
  methods: {
    runLesson(id) { },
    playSound(lesson) {
      let path = "/" + mp3FileName(lesson);
      if (this.audio != null) {
        this.audio.pause()
        this.audio = null;
      }
      this.audio = new Audio(path);
      this.audio.loop = false;
      this.audio.play();
    }
  },
  created() {
    eventBus.$emit('select-lesson', this.id);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
  font-size: 30px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



