/*eslint-disable */

import Live2d from './Live2d';
import VoiceRecognition from './VoiceRecognition';
import { mp3FileName } from '../helper';
import Determine from './Determine';
import Score from './Score';
import eventBus from '../components/EventBus.vue';
import { STATE, LIVE2D_STATE } from './state';
import { resoucePath } from '../helper';

export default class Speech {
  constructor(lesson, mode, dialog) {
    this.limitSec = 8;

    this.mode = mode;
    this.dialog = dialog;
    // 음성 합격 평가
    this.determine = new Determine(this.mode);
    // 점수
    this.score = new Score(dialog);

    // Updater 구동용
    this.live2d = new Live2d(lesson, dialog);
    this.state = STATE.LOADING;
    this.progress = 0;
    // 음성인식 시작
    this.voiceRecognition = new VoiceRecognition();
    // this.live2d.PlaySound('static/assets/haru/sounds/shake_00.mp3')
    // this.micPercent = 0;
    this.speechStartTime;

    this.Update();
  }

  EffectSound(filename) {
    let path = resoucePath(true) +  '/sound/effect/' + filename;
    if (this.audio != null) {
      this.audio.pause();
      this.audio = null;
    }
    this.audio = new Audio(path);
    this.audio.loop = false;
    this.audio.play();
  }

  // 상태 설정하기
  SetState(step) {
    this.progress = step;
    let _this = this;
    let dialog = null;
    for (let i = 0; i < this.dialog.length; i++) {
      if (parseInt(this.dialog[i].order) === step + 1) {
        dialog = this.dialog[i];
        break;
      }
    }
    // 다이얼로그가 없으면 끝~
    if (dialog === null) {
      this.state = STATE.END;
      return false;
    }

    // 소리 재생 모드
    this.live2d.micShow(false);
    if (parseInt(dialog.talk) === 0) {
      this.state = STATE.PLAYSOUND;
      this.live2d.PlaySound(mp3FileName(dialog));
      this.live2d.SetText(dialog.content, 'say');
      return true;
    }

    // 스피치 모드
    this.state = STATE.SPEECH;
    let text = this.determine.SetSource(
      this.dialog[this.progress].content,
      this.dialog[this.progress].alternative,
      this.limitSec
    );
    this.live2d.SetText(text, 'talk');
    this.voiceRecognition.Start();
    this.live2d.micShow(true);
    this.speechStartTime = new Date().getTime();
    return true;
  }

  // 캐릭터 말하는 중 업데이트
  _updateSpeech() {
    const _this = this;

    let vrText = this.voiceRecognition.GetText();
    let data = this.determine.CheckSuccess(vrText);

    // 스킵하기
    if (this.live2d.bSkip) {
      this.voiceRecognition.Stop();
      this.live2d.micShow(false);
      this.live2d.bSkip = false;
      this.state = STATE.SPEECH_END;
      //스킵에서 점수 넣기 : 0점
      // console.log(data);
      this.score.SetScore(this.progress, data);
      setTimeout(function() {
        _this.progress++;
        _this.SetState(_this.progress);
      }, 500);
    }

    // Time Out
    if (data.time === 0) {
      this.voiceRecognition.Stop();
      this.live2d.micShow(false);
      this.live2d.bSkip = false;
      this.state = STATE.SPEECH_FAIL;
      this.EffectSound('vr-failure.mp3');
      this.score.SetScore(this.progress, data);
      setTimeout(function() {
        _this.progress++;
        _this.SetState(_this.progress);
      }, 1500);
    }

    this.live2d.micPercent(this.speechStartTime, this.limitSec);
    this.live2d.SetText(data.display, 'talk');

    // 결과 판정
    if (data.result) {
      this.voiceRecognition.Stop();
      this.live2d.micShow(false);
      this.state = STATE.SPEECH_END;
      this.EffectSound('vr-success.mp3');
      // console.log("TRY count: " + this.voiceRecognition.tryCount);
      this.score.SetScore(this.progress, data);
      // 2초 후에 다음이벤트로 보내기
      setTimeout(function() {
        _this.progress++;
        _this.SetState(_this.progress);
      }, 1500);
    }
  }

  // 결과 화면 업데이트
  _updateEnd() {
    const _this = this;
    this.EffectSound('lesson-clear.mp3');
    this.state = STATE.END_DELAY;
    // 2초 후에 결과 화면으로
    setTimeout(function() {
      // window.history.back();
      _this.Stop();
      eventBus.$emit('lesson-end', _this.score.GetScores());
    }, 2000);
  }

  Update() {
    const _this = this;

    switch (this.state) {
      case STATE.LOADING:
        // 리소스 로딩 중
        if (this.live2d.state == LIVE2D_STATE.PLAY) {
          this.state = STATE.LOADING_COMPLETE;
        }
        break;
      case STATE.LOADING_COMPLETE:
        this.state = STATE.START_DELAY;
        let _this = this;
        // 1.5초 후 시작 하기
        setTimeout(() => {
          console.log('start');
          _this.Start();
        }, 1500);
        break;
      case STATE.START_DELAY:
        // 시작 대기 시간.
        break;
      case STATE.PLAYSOUND:
        if (this.live2d.isSoundPlaying == false) {
          this.progress++;
          this.SetState(this.progress);
        }
        break;
      case STATE.SPEECH_START:
        break;
      case STATE.SPEECH:
        this._updateSpeech();
        break;
      case STATE.SPEECH_END:
        break;
      case STATE.SPEECH_RETRY:
        break;
      case STATE.END:
        this._updateEnd();
        break;
      case STATE.END_DELAY:
        break;
    }

    this.handle = window.requestAnimationFrame(function() {
      _this.Update();
    });
  }

  Start() {
    // 현재 진행
    this.live2d.micShow(false);
    this.progress = 0;
    this.SetState(this.progress);
  }

  Stop() {
    if (this.handle) {
      window.cancelAnimationFrame(this.handle);
      this.handle = 0;
    }
  }

  Destroy() {
    if (this.handle) {
      window.cancelAnimationFrame(this.handle);
      this.handle = 0;
    }
    this.live2d.Destroy();
    this.voiceRecognition.Destroy();
  }
}
/*eslint-enable */



