<template>
  <div>
    <v-layout row>
      <v-flex xs12 sm8 offset-sm1>
        <v-card>
          <v-toolbar :class="{red:mode=='traning', green:mode=='practice'}" dark>
            <v-toolbar-title> {{ id }}. {{ mode|uppercase }} / {{ lesson.title }}</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon class="white--text" @click="textRightAlign=!textRightAlign">
              <v-icon>short_text</v-icon>
            </v-btn>
          </v-toolbar>
          <v-list three-line>
            <template v-for="d in dialogs">
              <v-list-tile avatar v-if="parseInt(d.talk)" @click="playSound(d)">
                <v-list-tile-avatar>
                  <v-icon large class="blue--text">face</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-html="d.resultText"></v-list-tile-title>
                  <v-list-tile-sub-title v-html="d.trans"></v-list-tile-sub-title>
                  <v-list-tile-sub-title>
                    <v-icon v-for="n in 5" :key="n" :class="{'pink--text':d.score >= n*20}">star</v-icon>
                  </v-list-tile-sub-title>

                </v-list-tile-content>
              </v-list-tile>

              <v-list-tile avatar v-else @click="playSound(d)">
                <v-list-tile-action>
                  <v-icon v-if="d.icon" class="pink--text">fa-star</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title :class="{'text-xs-right':textRightAlign}" v-text="d.content"></v-list-tile-title>
                  <v-list-tile-sub-title :class="{'text-xs-right':textRightAlign}" v-html="d.trans"></v-list-tile-sub-title>
                </v-list-tile-content>
                <v-list-tile-avatar>
                  <v-icon large>person</v-icon>
                  <!-- <img v-bind:src="d.model" /> -->
                </v-list-tile-avatar>
              </v-list-tile>

              <v-divider></v-divider>
            </template>
          </v-list>
        </v-card>
        <v-toolbar  :class="{red:mode=='traning', green:mode=='practice'}" dark>
          <v-toolbar-title>SCORE : {{ score }}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn primary @click="$router.push({name:'lesson', params:{mode: mode, id:id}})">RETRY</v-btn>
        </v-toolbar>

      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import lessons from '../data/lessons'
import eventBus from './EventBus.vue'
import { mp3FileName } from '../helper'

export default {
  data: function() {
    return {
      textRightAlign: true
    }
  },
  props: [
    'mode',
    'id'
  ],
  computed: {
    lesson: function() {
      let id = this.id - 1
      return lessons[id]
    },
    dialogs: function() {
      return this.$store.state.lessonData;
    },
    score: function() {
      let f = this.dialogs.filter((d) => {
        return d.talk;
      });
      let score = 0;
      for (let i = 0; i < f.length; i++) {
        score += f[i].score;
      }
      return parseInt(score / f.length);
    }
  },
  created() {
    eventBus.$emit('select-lesson', this.id);
    console.log(this.id);
  },
  methods: {
    playSound(lesson) {
      let path = "/" + mp3FileName(lesson);
      if (this.audio != null) {
        this.audio.pause()
        this.audio = null;
      }
      this.audio = new Audio(path);
      this.audio.loop = false;
      this.audio.play();
    }
  },
  filters: {
    uppercase: function(v) {
      return v.toUpperCase();
    }
  }
}
</script>

<style scoped>
h1,
h2 {
  font-weight: normal;
  font-size: 30px;
}

span.gray--text {
  color: lightgray;
}
</style>



