const MODE = {
  PRACTICE: 'practice',
  TRANING: 'traning'
};

export default class Determine {
  constructor(mode) {
    this.text = '';
    this.textSplit = [];
    this.match = [];
    this.limitSec = 0;
    this.timer;
    this.mode = mode;
  }

  RemoveSigns(text) {
    return text
      .replace(/,/g, '')
      .replace(/\./g, '')
      .replace(/!/g, '')
      .replace(/\?/g, '')
      .replace(/'/g, '')
      .replace(/"/g, '')
      .trim()
      .toLowerCase();
  }

  SetSource(text, alternative, limitSec) {
    this.text = text;
    this.textSplit = this.RemoveSigns(this.text).split(' ');
    // console.log(this.textSplit);
    this.match = new Array(this.textSplit.length);
    for (let i = 0; i < this.match.length; i++) {
      this.match[i] = false;
    }

    this.alternative = this.RemoveSigns(alternative).split('|');

    // 시간 체크 시작
    this.limitSec = limitSec;
    let _this = this;
    if (this.timer) {
      clearInterval(this.timer);
    }
    this.timer = setInterval(fn => {
      _this.limitSec--;
    }, 1000);

    if (this.mode === MODE.TRANING) {
      return text.replace(/[a-zA-z0-9]/g, '_');
    }
    return text;
  }

  CheckSuccess(text) {
    if (this.limitSec === 0) {
      clearInterval(this.timer);
      // 시간 제한으로 끝남.
      let matchText = '';
      let resultText = '';
      let origin = this.text.split(' ');
      for (let i = 0; i < this.match.length; i++) {
        if (this.match[i] === true) {
          matchText += '<pass>' + origin[i] + '</pass> ';
          resultText += origin[i] + ' ';
        } else {
          resultText += '<span class="gray--text">' + origin[i] + '</span> ';
          matchText += origin[i] + ' ';
        }
      }
      return {
        result: false,
        display: matchText,
        resultText: resultText,
        match: this.match,
        time: this.limitSec
      };
    }

    if (!text) {
      let display = this.text;
      if (this.mode === MODE.TRANING) {
        display = display.replace(/[a-zA-z0-9]/g, '_');
      }
      return {
        result: false,
        display: display,
        resultText: '<span class="gray--text">' + this.text + '</span> ',
        match: this.match,
        time: this.limitSec
      };
    }

    console.log(this.text + ' => ' + text);
    let target = this.RemoveSigns(text).split(' ');

    for (let i = 0; i < target.length; i++) {
      for (let j = 0; j < this.textSplit.length; j++) {
        if (target[i] === this.textSplit[j]) {
          this.match[j] = true;
        }
      }
    }

    // 대체 문장
    for (let i = 0; i < this.alternative.length; i++) {
      if (this.alternative[i].trim() === this.RemoveSigns(text)) {
        for (let j = 0; j < this.match.length; j++) {
          this.match[j] = true;
        }
      }
    }

    let result = true;
    for (let i = 0; i < this.match.length; i++) {
      if (this.match[i] === false) {
        result = false;
        break;
      }
    }

    let matchText = '';
    let resultText = '';
    let origin = this.text.split(' ');
    for (let i = 0; i < this.match.length; i++) {
      if (this.match[i] === true) {
        matchText += '<pass>' + origin[i] + '</pass> ';
        resultText += origin[i] + ' ';
      } else {
        if (this.mode === MODE.TRANING) {
          matchText += origin[i].replace(/[a-zA-z0-9]/g, '_') + ' ';
        } else {
          matchText += origin[i] + ' ';
        }

        resultText += '<span class="gray--text">' + origin[i] + '</span> ';
      }
    }

    if (result === true) {
      clearInterval(this.timer);
    }
    // console.log(this.match);
    return {
      result: result,
      display: matchText,
      resultText: resultText,
      match: this.match,
      time: this.limitSec
    };
  }
}



