export function leftPad(number, targetLength) {
  var output = number + "";
  while (output.length < targetLength) {
    output = "0" + output;
  }
  return output;
}

export function resoucePath(flag) {
  if (flag) {
    return process.env.NODE_ENV === 'development' ? '/static' : '/talklish';
  }
  return process.env.NODE_ENV === 'development' ? 'static' : 'talklish';
}

export function mp3FileName(lesson) {
  let path = resoucePath(false) + '/sound/';
  return path + leftPad(lesson.lesson, 3) + '_' + lesson.category + '_' + leftPad(lesson.order, 2) + '.mp3'
}



