export default class Score {
  constructor(dialog) {
    this.dialog = dialog;
    for (let i = 0; i < dialog.length; i++) {
      dialog[i].score = 0;
      dialog[i].match = [];
      dialog[i].resultText = '';
    }
  }

  SetScore(num, data) {
    if (num >= this.dialog.length) {
      return false;
    }
    let goodCount = 0;
    for (let i = 0; i < data.match.length; i++) {
      if (data.match[i] === true) {
        goodCount++;
      }
    }
    this.dialog[num].score = parseInt(100 / data.match.length * goodCount);
    this.dialog[num].match = data.match;
    this.dialog[num].resultText = data.resultText;
    return false;
  }

  GetScores() {
    return this.dialog;
  }
}



